"use strict";
class Exception {
    constructor(message, name) {
        this.message = message;
        this.name = name;
    }
}
exports.Exception = Exception;
(function (Exception) {
    class NetworkError extends Exception {
        constructor(message) {
            super(message, "NetworkError");
        }
    }
    Exception.NetworkError = NetworkError;
    class ChuniNetSessionNotFound extends Exception {
        constructor(message) {
            super(message, "ChuniNetSessionNotFound");
        }
    }
    Exception.ChuniNetSessionNotFound = ChuniNetSessionNotFound;
    class ChuniNetSessionDisconnected extends Exception {
        constructor(message) {
            super(message, "ChuniNetSessionDisconnected");
        }
    }
    Exception.ChuniNetSessionDisconnected = ChuniNetSessionDisconnected;
    class ChuniNetLoginError extends Exception {
        constructor(message) {
            super(message, "ChuniNetLoginError");
        }
    }
    Exception.ChuniNetLoginError = ChuniNetLoginError;
    class ErrorResponse extends Exception {
        constructor(message) {
            super(message, "ErrorResponse");
        }
    }
    Exception.ErrorResponse = ErrorResponse;
    class MusicNotFound extends Exception {
        constructor(message) {
            super(message, "MusicNotFound");
        }
    }
    Exception.MusicNotFound = MusicNotFound;
})(Exception = exports.Exception || (exports.Exception = {}));



